'use client'
import styles from '@/styles/progress/progress.module.css'
import Image from 'next/image'
import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

const Progress = () => {
  const progressRef = useRef(null)

  useEffect(() => {
    if (typeof window === 'undefined') return

    const sections = gsap.utils.toArray(`.${styles.progressBox}`)

    sections.forEach((el) => {
      gsap.fromTo(
        el,
        { opacity: 0, y: 100, scale: 0.95, filter: 'blur(10px)' },
        {
          opacity: 1,
          y: 0,
          scale: 1,
          filter: 'blur(0px)',
          duration: 1.2,
          ease: 'power3.out',
          scrollTrigger: {
            trigger: el,
            start: 'top 85%',
            toggleActions: 'play none none reverse',
          },
        }
      )
    })

    gsap.fromTo(
      `.${styles.progressbar}`,
      { width: '0%' },
      {
        width: '100%',
        duration: 1.8,
        ease: 'power3.out',
        scrollTrigger: {
          trigger: `.${styles.progressbar}`,
          start: 'top 90%',
          toggleActions: 'play none none reverse',
        },
      }
    )

    gsap.fromTo(
      `.${styles.imgarea} img`,
      { scale: 0.8, opacity: 0.5, filter: 'blur(5px)' },
      {
        scale: 1,
        opacity: 1,
        filter: 'blur(0px)',
        duration: 1.5,
        ease: 'power3.out',
        scrollTrigger: {
          trigger: `.${styles.imgarea}`,
          start: 'top 85%',
          toggleActions: 'play none none reverse',
        },
      }
    )
  }, [])

  return (
    <section className="sec progress" ref={progressRef}>
      <section className="secInner">
        <div className="titleArea">
          <Image
            src="/images/section/PROGRESS.svg"
            width={693}
            height={158}
            alt="PROGRESS"
          />
          <h2 className="secTitle">これまでの軌跡</h2>
        </div>
        <section className={styles.progressArea}>
          {[
            {
              year: '2015.4-2016.8',
              building: '帝都渋谷ビル',
              size: '間借り5坪',
              imgSrc: '/images/section/progressSection/progress01.png',
              imgAlt: '帝都渋谷ビル',
              index: '01',
            },
            {
              year: '2016.8-2017.3',
              building: '金谷ホテルマンション',
              size: '間借り10坪',
              imgSrc: '/images/section/progressSection/progress02.png',
              imgAlt: '金谷ホテルマンション',
              index: '02',
            },
          ].map((item, idx) => (
            <article key={idx} className={styles.progressBox}>
              <div className={styles.textarea}>
                <div className={styles.progressYear}>
                  <p className="bunyan-bold">{item.year}</p>
                </div>
                <div className={styles.progressBuilding}>
                  <p>{item.building}</p>
                  <p>{item.size}</p>
                </div>
              </div>
              <div className={styles.progressbar}></div>
              <div className={styles.imgarea}>
                <figure>
                  <Image
                    src={item.imgSrc}
                    width={296}
                    height={368}
                    alt={item.imgAlt}
                  />
                </figure>
                <div className={styles.imgareaText}>
                  <span className="bunyan-regular">{item.building}</span>
                  <span></span>
                  <span className="bunyan-regular">{item.index}</span>
                </div>
              </div>
            </article>
          ))}
        </section>
      </section>
    </section>
  )
}

export default Progress
